body {
  background-color: #6d8ded;
}

.search-form {
  width: 350px;
  text-align: center;
  background-color: #9bb3fa;
  border-radius: 20px;
  margin: 20px auto;
  padding: 10px;
  font-size: 20px;
}

.input {
  width: 100%;
  border: none;
  border-radius: 5px;
  margin: 10px 0;
  padding: 10px;
  font-size: 20px;
}

button {
  color: #3a4566;
  background-color: #edf947;
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
  font-size: 20px;
}

.gallery {
  flex-wrap: wrap;
  justify-content: space-between;
  gap: 20px;
  padding: 20px;
  display: flex;
}

.load-more {
  color: #3a4566;
  background-color: #edf947;
  border: none;
  border-radius: 5px;
  margin: 20px auto;
  padding: 10px 20px;
  font-size: 20px;
  display: block;
}

.photo-card {
  border: 1px solid #ccc;
  border-radius: 5px;
  flex-basis: calc(25% - 40px);
  margin-bottom: 10px;
  padding: 10px;
}

.photo-card img {
  width: 100%;
  object-fit: cover;
}

.photo-card .info {
  margin-top: 10px;
}

.photo-card .info-item {
  color: #2d3343;
}

.modal {
  width: 100%;
  height: 100%;
  text-align: center;
  display: none;
  position: fixed;
}

.modal-img {
  max-width: 90%;
  max-height: 90%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.close-btn {
  font-size: 20px;
  position: absolute;
  top: 10px;
  right: 10px;
}

